import React, { useEffect, useState } from "react";
import "../Styles/OverlayStyle.css";
import "../Styles/OverlayLightStyle.css";
import Close from "../Images/Close.svg";

export default function Overlay({ onClose }) {
    const [theme, setTheme] = useState(window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light");

    useEffect(() => {
        const mediaQuery = window.matchMedia("(prefers-color-scheme: dark)");
        const handleThemeChange = (e) => {
            setTheme(e.matches ? "dark" : "light");
        };

        mediaQuery.addListener(handleThemeChange);

        return () => {
            mediaQuery.removeListener(handleThemeChange);
        };
    }, []);

    useEffect(() => {
        if (theme === "dark") {
            import("../Styles/OverlayStyle.css");
        } else {
            import("../Styles/OverlayLightStyle.css");
        }
    }, [theme]);

    return (
        <div className={`overlay ${theme}`}>
            <div className={`overlay-content ${theme}`}>
                <img src={Close} alt="Close" className="close-btn" onClick={onClose} />
                <h2> </h2>
                <p> WATA Group Limited,    "",     
                      :</p>
                <p>1.  
                           , ,   , 
                       ,  ,     
                    .</p>
                <p className={"p2"}>2.  </p>
                <p className={"p2"}>2.1. ,    ,   
                         ,  .</p>
                <p className={"p2"}>2.2.          
                    .</p>
                <p className={"p2"}>2.3.         ,
                        .</p>
                <p className={"p2First"}>3.  </p>
                <p className={"p2"}>3.1.         ,
                          .</p>
                <p className={"p2"}>3.2.           
                         30      .</p>
                <p className={"p2First"}>4.  </p>
                <p className={"p2"}>4.1.       ,   ,
                         .</p>
                <p className={"p2"}>4.2.          
                             .</p>
                <p className={"p2First"}>5. </p>
                <p className={"p2"}>5.1.         
                         .</p>
                <p className={"p2"}>5.2.          
                    ,   ,  .</p>
                <p className={"p2First"}>6.  </p>
                <p className={"p2"}>6.1.         
                     .</p>
                <p className={"p2"}>6.2.      ,    
                         .</p>
                <p className={"p2"}>6.3.            
                          .</p>
            </div>
        </div>
    );
}
