import SVG from "../../icons/svg"
import Button from "../../button"
import { useIsModal } from "../../hooks/is-modal"
import "./style.css"



const ModalAgreement = ({ show, setShow }) => {
    useIsModal({show})

    if (!show) return null

    return (
        <div className={"root"}>
            <div className={"background"} />
            <div className={"modal"}>
                <div className={"header"}>
                    <h1 className={"title"}> </h1>
                    <span className={"close"} onClick={() => setShow(false)}>{SVG.Close}</span>
                </div>
                <div className={"content"}>
                    <p className={"text"}>        :</p>
                    <br />
                    <p className={"text"}>1.  <br />
                               , ,   ,     ,  ,      .</p>
                    <br />
                    <p className={"text"}>2.  <br />
                        2.1. ,    ,         ,  .<br />
                        2.2.           .<br />
                        2.3.         ,     .</p>
                    <br />
                    <p className={"text"}>3.  <br />
                        3.1.         ,       .<br />
                        3.2.                 30      .</p>
                    <br />
                    <p className={"text"}>4.  <br />
                        4.1.       ,   ,      .<br />
                        4.2.                    .</p>
                    <br />
                    <p className={"text"}>5. <br />
                        5.1.               .<br />
                        5.2.           ,   ,  .</p>
                    <br />
                    <p className={"text"}>6.  <br />
                        6.1.           .<br />
                        6.2.      ,          .<br />
                        6.3.                   .</p>
                </div>
                <div className={"footer"}>
                    <Button
                        title="   "
                        className={"button"}
                        onClick={() => setShow(false)}
                    />
                </div>
            </div>
        </div>
    )
}

export default ModalAgreement